/**
  @hash,hash
  @base,url
  @query,
  @return url
*/
export default function (hash, base, query) {
  // 
  let filename = hash.slice(0, 1) + '/' + hash.slice(1, 3) + '/' + hash.slice(3)
  // 
  let matches = filename.match(/png|jpeg|jpg$/)
  return `${base}/${filename}.${matches[0]}?${query}`
}



