<template>
  <div class="shop">
    <!--  -->
    <elm-top :seller="seller"></elm-top>
    <!--  -->
    <nav>
      <ul>
        <li @click="current='elm-goods'" :class="{active:current=='elm-goods'}"><span></span></li>
        <li @click="current='elm-ratings'" :class="{active:current=='elm-ratings'}"><span></span></li>
        <li @click="current='elm-seller'" :class="{active:current=='elm-seller'}"><span></span></li>
      </ul>
    </nav>
    <!--  -->
    <component :is="current" :goods="goods" :seller="seller"></component>
  </div>
</template>

<script>
import { getGoods, getRatings, getRestaurant } from '@/api/shop'
import Top from './Top'
import Goods from './Goods'
import Ratings from './Ratings'
import Seller from './Seller'

export default {
  data () {
    return {
      current: 'elm-goods', // 
      goods: [], // 
      ratings: [], // 
      seller: {}, // 
      hasMore: true, // 
      page: 1, // 
      limit: 10 // 
    }
  },
  created () {
    let id = this.$route.query.id
    console.log(id)
    // apigoods
    getGoods(id).then(res => {
      console.log(res)
      this.goods = res.data
    }).catch(err => {
      console.log(err)
    })
    // api
    let offset = (this.page - 1) * this.limit
    getRatings(id, this.hasMore, offset, this.limit).then(res => {
      console.log(res)
      this.ratings = res.data
    }).catch(err => {
      console.log(err)
    })
    // api
    getRestaurant(id).then(res => {
      console.log(res)
      this.seller = res.data
    }).catch(err => {
      console.log(err)
    })
  },
  components: {
    'elm-top': Top,
    'elm-goods': Goods,
    'elm-ratings': Ratings,
    'elm-seller': Seller
  }
}
</script>

<style lang="less" scoped>
.shop {
  position: relative;
  nav {
    line-height: 1.066667rem;
    height: 1.066667rem;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    ul {
      display: flex;
      justify-content: space-around;
      font-size: .373333rem;
      color: #666;
      border-bottom: 1px solid #ddd;
      li.active{
        span {
          color: #3190e8;
          font-weight: 700;
          position: relative;
          &::after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            height: .08rem;
            bottom: -.266667rem;
            background: #2395ff;
          }
        }
      }
    }
  }
  }
</style>



